.v-enter[data-v-55f77fe4],
.v-leave-to[data-v-55f77fe4] {
  opacity: 0;
  -webkit-transform: translateX(100px);
          transform: translateX(100px);
}
.v-enter-active[data-v-55f77fe4],
.v-leave-active[data-v-55f77fe4] {
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}
/* 固定写法,配合使用实现列表的平稳动画 */
.v-move[data-v-55f77fe4] {
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}
.v-leave-active[data-v-55f77fe4] {
  position: absolute;
}
#tree[data-v-55f77fe4] {
  display: block;
  position: relative;
  padding: 5px 15px;
}
#tree ul[data-v-55f77fe4] {
  position: relative;
  padding-left: 60px;
  margin: 0;
}
#tree ul > li[data-v-55f77fe4] {
  position: relative;
  padding: 5px 0;
}
#tree > ul[data-v-55f77fe4] {
  padding: 0;
  margin: 0;
}
/* 水平线 */
#tree > ul ul > li[data-v-55f77fe4]:after {
  content: "  ";
  position: absolute;
  top: 30px;
  left: -45px;
  width: 45px;
  border-top: 1px solid #ddd;
}
/** 垂直方向连线 */
#tree ul > li[data-v-55f77fe4]:not(:last-child):before {
  content: " ";
  position: absolute;
  top: 0;
  left: -45px;
  height: 100%;
  border: none;
  border-left: 1px solid #ddd;
}
#tree ul > li[data-v-55f77fe4]:last-child:before {
  content: " ";
  position: absolute;
  top: 0;
  left: -45px;
  height: 30px;
  border: none;
  border-left: 1px solid #ddd;
}
ul[data-v-55f77fe4] {
  list-style-type: none;
  text-align: left;
}
span[data-v-55f77fe4] {
  display: inline-block;
  text-align: center;
  height: 45px;
  color: #888;
  line-height: 45px;
  border: 1px solid #ddd;
  border-radius: 5px;
  padding: 0 8px;
  font-size: 1.5em;
  cursor: pointer;
}
/* 获得焦点改变背景色 */
#tree span[data-v-55f77fe4]:hover,
#tree span:hover + ul span[data-v-55f77fe4] {
  color: #fff;
  background-color: deepskyblue;
}
/* 获得焦点改变边框和连线的颜色 */
#tree span[data-v-55f77fe4]:hover,
#tree span:hover + ul span[data-v-55f77fe4],
#tree span:hover + ul li[data-v-55f77fe4]:before,
#tree span:hover + ul li[data-v-55f77fe4]:after {
  border-color: blue;
}

